<template>
	<view>
		<view class="trading">
			<view class="tradingTitle">
				交易规则
			</view>
			<view class="oneTitle">
				1.关于取消订单规定特殊说明：
			</view>
			<view class="twoTitle">
				（1）关于订单退款做如下规定：
			</view>
			<text space="nbsp">
				因蛋糕都是新鲜现做，在您下单预约配送时间往前倒推（T+1）小时内可以接受退款，如超过T+1小时则不接受订单退款T：是指该城市地区某产品的提前预约配送时效，如深圳榴芒双拼蛋糕需提前2小时预约配送时效，则深圳的T为2；
				特殊产品（如30磅鹏程万里需提前24小时预订）的T根据该产品的提前预约时效决定；</text>
			<text>举例：深圳的小张在3月10日上午9点预订了2磅榴芒双拼蛋糕在当天下午20点-21点期间配送，则小王可以在当天17点前（20-（2+1）=17）可以取消，如超过当天17点后则不接受订单取消和退款；</text>
			<view class="twoTitle">
				（2）订单退款时效说明：
			</view>
			<text>如订单满足退款时限和其他规定的，会在发起退款后的7天之内完成退款操作（如遇节假日会顺延）。 如有任何疑问请致电：400-999-6666</text>
			<view class="oneTitle">
				2.如遇订单问题如何处理？
			</view>
			<text>如需修改订单收货时间和地址及其他订单问题，请至少在收货时间的提前一天联系微信在线客服确认（服务时间：08:30-21:30）或致电客服电话400-999-6666修改（工作时间08:30-22:30）
				温馨提示：您可以下单预约12个月内的任何指定日期时间收货，如遇到促销活动您可以提前下单囤货，不错过每一次优惠机会，如遇收货时间修改需至少提前1天联系400电话客服或微信在线客服修改收货时间和地址；</text>
			<view class="oneTitle">
				3.关于订单退款
			</view>
			<view class="twoTitle">
				（1）如何申请订单退款？
			</view>
			<text>方法一：您须现场填写由配送员提供的《信息反馈表》，若该订单是“货到付款”的即现场免单；若该订单是“在线支付”的我司将会在7个工作日内办理审核及退款手续。</text>
			<text>方法二：拨打客服热线：400-999-6666或进入【幸福西饼】微信公众号联系在线客服提交申请退款需求，客服每日服务时间：8:30-21:30；</text>
			<view class="twoTitle">
				（2）申请退款多久能到账？
			</view>
			<text>当您的订单退款申请发起审核通过后，一般款项将会7日左右根据您支付的选择路径原路返回给您账户，如有疑问可以咨询有官方客服：400-999-6666。 texts:
				请您在下单时尽量使用微信零钱包付款,如发生退款情况，款项能更快捷退回给您；</text>
			<view class="twoTitle">
				（3）因买家原因导致订单配送不成功，会拒绝订单退款说明：
			</view>
			<text>请您务必在配送当天保持手机畅通以便订单准时送达，如因顾客自身原因（例如：电话关机联系不上、地址不详细等）导致无法配送成功，由于新鲜蛋糕保质期较短则由配送中心将蛋糕作报废登记处理，此种配送过不成功情况无法改期配送或者办理退款，不便之处，敬请谅解！</text>
			<view class="twoTitle">
				（4）已申请退款订单审核过程中买家撤回退款说明：
			</view>
			<text>买家已经通过订单后台申请退款订单，由于订单退款需要财务审核确认会有过程，当您申请退款后您的订单将不会安排继续制作，如果您需要取消退款继续需要产品，请您一定通过电话400-999-6666，或者微信在线客服留言确认才能确保该订单重新下单正常收货的，请您谅解！</text>
			<view class="twoTitle">
				（5）因顾客原因首次配送不成功，符合二次配送的规定特殊说明：
			</view>
			<text>因顾客原因导致订单产生二次配送说明：如因您的自身原因（例如：电话关机联系不上、地址不详细等）导致配送员第一次送达后无法正常签收，在当天内符合二次配送规定的，如需要第二次配送的情况会产生额外的配送费用（费用视实际情况收取），不便之处，敬请谅解！</text>
		</view>
		<view class="guide">
			<view class="tradingTitle">
				幸福西饼平台侵权投诉指引
			</view>
			<text>幸福西饼作为国内知名O2O电商蛋糕品牌之一，一直致力于建立一个公平、公开、公 正的平台，幸福西饼作为平台服务提供方，始终坚持诚信守约、进取担当的态度与第三方合作伙伴携手并进，建立和维护良性互动、健康有序的平台秩序。</text>
			<text>为了建立良好的市场秩序和保护权利人的合法权益，根据相关法律法规的规定，幸福西 饼特制定本指引，如果您在使用幸福西饼平台服务中发现相关内容侵犯了您的权益，您可以通过以下途径通知我们，我们将依法进行处理。</text>
			<text>特别提醒：向本平台发起侵权投诉，是一项正式的、严肃的、具有法律意义的行为， 包括但不限于证明材料的选择和提交等流程，都会涉及大量中国大陆法律专业知识。若侵权投诉不能完全符合法定条件，平台将无法处理。若侵权投诉存在错误或恶意投诉，根据 《中国人民共和国电子商务法》第四十二条的规定，需依法承担赔偿责任。</text>
			<view class="oneTitle">
				一、流程
			</view>
			<view class="twoTitle">
				1.投诉内容
			</view>
			<text>若您在使用幸福西饼平台时，发现存在如下情况，可向幸福西饼平台申请投诉：</text>
			<text>1.1.卖家在所发布的商品信息、图片或所使用的店铺名、域名等中不当使用他人权利；</text>
			<text>1.2.卖家出售商品涉嫌不当使用他人商标权、著作权、专利权等合法权利；</text>
			<text>1.3.卖家所发布的商品信息或所使用的其他信息造成消费者混淆、误认或造成不正当竞争。同一权利人在3日内对同一卖家的投诉视为一次投诉。</text>
			<text>出现违反本条所规定的侵权行为的，幸福西饼有权下架、删除卖家所发布的不当使用他人权利的商品或信息，同时，幸福西饼有权视情节严重程度采取店铺屏蔽、店铺关闭、限制发布商品等市场监管措施。</text>
			<view class="twoTitle">2.通知幸福西饼</view>
			<text>若权利人认为第三方在幸福西饼平台提供的商品或服务侵犯其合法权益，请按以下要求提交相关材料进行投诉，通知幸福西饼：</text>
			<text>权利人应向幸福西饼提出书面通知书（详见附件），通知书应包括但不限于以下内容：</text>
			<text>2.1权利人主体信息和相关材料</text>
			<text>①权利人的姓名（名称）、联系方式、地址及营业执照（单位）、身份证（个人）、相关授权证明等证明权利人主体资格的材料；</text>
			<text>②权利人如委托他人代理投诉的，还需提供授权委托书，以及代理人姓名（名称）、联系方式、地址及营业执照（单位）、身份证（个人）、相关代理人职务身份证明（公司授权书、介绍信等证明代理人职务身份及公司授权代理人代表公司办理相关投诉事务）等证明代理人主体资格的材料。</text>
			<text>2.2权利人要求</text>
			<text>权利人投诉侵权的商家信息，以及相关投诉事项说明。</text>
			<text>2.3构成侵权的初步证明材料</text>
			<text>该初步证明材料应包括：</text>
			<text>①权利人拥有权利的权属证明材料：包括但不限于相关有权机构颁发的版权证书、商标权证书、专利权证书、作品首次公开发表或发行日期证明材料、创作手稿、经权威机构签发的作品创作时间戳、作品备案证书等能证明权利人拥有相关权利的有效权属证明；</text>
			<text>②被投诉方提供的服务构成侵权的证明材料：包括但不限于被投诉方提供的服务构成对权利人版权、商标权或专利权等权利侵权的有效证明材料等。</text>
			<text>2.4权利人保证</text>
			<text>权利人应在通知书中明确保证：权利人在通知书中的陈述和提供的相关材料皆是真实、有效和合法的，并保证承担和赔偿幸福西饼根据权利人的通知书对相关帐号的处理而给幸福西饼造成的任何损失，包括但不限于幸福西饼因向被投诉方或用户赔偿而产生的损失及幸福西饼名誉、商誉损害等。</text>
			<text>通知书及相关证明材料准备时需注意的事项以及投寄地址详见下文注意事项中的说明。</text>
			<view class="twoTitle">3.幸福西饼反馈</view>
			<text>幸福西饼作为公正的平台服务者，收到权利人符合本指引要求的通知书后，会尽快按照相关法律法规的规定进行处理，并在符合法律规定要求的情形下将权利人的通知书转送给被投诉方。</text>
			<text>若被投诉方不认可权利人的投诉，幸福西饼会将被投诉方提供的相关材料转送给权利人，若权利人对于被投诉方的意见及其提供的相关材料有异议的，幸福西饼建议权利人另行通过行政投诉、诉讼等方式直接和被投诉方解决相关问题。如果权利人有新的并可充分推翻被投诉方意见的证明材料的，也可向幸福西饼提供。</text>
			<view class="oneTitle">二、注意事项</view>
			<text>1.本指引中的权利人，指拥有版权、商标权、专利权等合法权益的原始所有人，代理人是指经原始所有人合法授权的受托人，包括自然人、法人或其他组织等。</text>
			<text>2.为了确保权利人提供相关材料的真实性和有效性，权利人的书面通知书及相关证明材料，一般应提供原件，不能提供原件的，应提供复印件（在复印件上应有权利人的签章）。</text>
			<view class="oneTitle">三、通知方式</view>
			<text>I.通过邮件的方式将前述通知书及其他相关证明材料的扫描件发送至：legal@xfxb.net。</text>
			<text>II.通过邮寄的方式将前述书面通知书及其他相关证明材料的纸质版本邮寄至幸福西饼指定的以下地点：</text>
			<text>地址：中国广东省深圳市南山区同沙路168号凯达尔集团中心A座23楼法务部</text>
			<text>邮编：518057</text>
		</view>
	</view>
